.content {
	display: flex;
	flex-direction: column;
	height: 93vh;
	position: relative;
	justify-content: flex-start;
  background-color: #121a27;
  justify-content: center;

}

.gallery {
	display: flex;
	margin-left: 12vw;
	padding-right: 12vw;
}

header {
	width: 50vw;
	line-height: 0.8;
	margin: 23vh 11vw 0 7vw;


	.title1{
		font-family: 'Titre';
	  color:white;
	  font-size: 10vw;
		display: block;
		letter-spacing: -1vw;
	}

	.title1 span{
		animation-name: letter-wave;
		animation-duration: 0.5s;
		position: relative;
		top: 0;
		opacity: 1;
		transition: top 0.2s;
	}

	.title1 span:nth-child(2){
		animation-delay: 0.1s;
	}
	.title1 span:nth-child(3){
		animation-delay: 0.2s;
	}
	.title1 span:nth-child(4){
		animation-delay: 0.3s;
	}
	.title1 span:nth-child(5){
		animation-delay: 0.4s;
	}
	.title1 span:nth-child(6){
		animation-delay: 0.5s;
	}
	.title1 span:nth-child(7){
		animation-delay: 0.6s;
	}

	@keyframes letter-wave {
		50%{
			top: -25%;
		}
	}

	.title2{
		font-family: 'Titre';
		color:white;
		font-size: 1.5vw;
		display: block;
	}
}

.vanille, .noe{
	height: 110px;
	position:fixed;
	top: 83%;
	z-index: 2000;
}

.vanille{
	left: 48%;
}
.noe{
	left: 52%;
}
.gallery__item {
  margin: -8vh 180px 0 30px;
	display: flex;
	flex-direction: column;
	width: 420px;
	word-break: break-all;

	h2{
		font-family: 'Titre';
		color:white;
		text-align: center;
		margin: 10px;
	}

	.gallery__item-img{
		position: relative;

		img{
		  width: 100%;
			height: 100%;
		}

		.description{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-family: 'Titre';
			max-width: 0;
			overflow: hidden;
			opacity: 0;
			display: flex;
			justify-content:center;
			max-height: 80%;
			word-break: normal;

			p{
				color: white;
				text-align: center;
			}
		}

		&:hover .description{
			max-width: 80%;
			opacity: 1;
			transition: max-width	0.5s;
			transition-delay: 0.3s;
			transition-timing-function:ease-out;
		}

		&::before{
			content:'';
			display: block;
			width: 100%;
			height: 100%;
			background-color: rgba(46, 46, 46, 0.5);
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			transition: opacity 0.2s;
		}

		&:hover::before{
			opacity: 1;

		}

		.macaron{
			position: absolute;
			width: 150px;
			top:-75px;
			left: -75px;
			font-family: Titre;
			font-size: 7em;
			animation-name: rotate-macaron;
			animation-duration: 60s;
			animation-iteration-count: infinite;
			transform: rotate(0deg);
			animation-timing-function: linear;
			letter-spacing: 10px;
			border-radius: 50%;
			border: solid 1px #b4630e;
		}

		.macaron:hover{
			animation-name: macaron-hover;
			animation-duration: 0.5s;
			animation-iteration-count: 1;
		}

		@keyframes rotate-macaron{
			100%{
				transform: rotate(360deg);
			}
		}

		@keyframes macaron-hover{
			100%{
				transform: rotate(360deg);
			}
		}
	}
}
